@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500&display=swap);
:root {
  --var-primary-color: #ec6500;
  --var-secondary-color: #3a3939;
  --var-terceary-color: white;
  --ssm: 12px;
  --sm: 14px;
  --md: 16px;
  --lg: 18px;
}

html {
  font-size: 62.5%;
}
body {
  margin: 0;
  font-family: "Poppins", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App{text-align:center}

.FinamigaTestBar{width:100%;display:flex;padding:1.6rem;box-sizing:border-box;align-items:center;background-color:var(--var-terceary-color);box-shadow:0px 0px 1px #000;text-decoration:none}.FinamigaTestBar img{width:140px;height:35px}.FinamigaTestBar h1{color:var(--var-secondary-color);margin:0px;margin-left:20px}

.Container{display:flex;padding:0px 60px}.Container .Wrap{display:flex;flex-direction:column;box-sizing:content-box;margin:50px 0px;width:100%}.Container .Wrap .Wrap__List{display:grid;grid-gap:35px}.Container .search{width:100%;position:relative;display:flex;align-self:center}.Container .search .searchTerm{width:100%;border:3px solid var(--var-primary-color);padding:10px;height:20px;border-radius:16px;outline:none;color:#9dbfaf;margin-bottom:60px}.Container .search .searchTerm:focus{color:var(--var-secondary-color);font-size:var(--sm)}@media (min-width: 645px){.Wrap .Wrap__List{grid-template-columns:repeat(2, 1fr)}}@media (min-width: 845px){.Wrap .Wrap__List{grid-template-columns:repeat(3, 1fr)}}@media (min-width: 1000px){.Wrap .Wrap__List{grid-template-columns:repeat(4, 1fr)}}

.List__Card{border-radius:16px;overflow:hidden;transition:all 0.3s;background-color:var(--var-terceary-color);box-shadow:10px 10px 10px #bebebe, -10px -10px 10px #ffffff}.List__Card:hover{-webkit-transform:scale(1.08);transform:scale(1.08)}.List__Card img{width:100%;object-fit:cover}.List__Card .List__Card__info h2{color:var(--var-terceary-color);background-color:var(--var-primary-color);margin:0px;padding:0px}.List__Card .List__Card__info .Card__info p{display:flex;justify-content:space-evenly;font-size:var(--sm)}.List__Card .List__Card__info .Card__info p:last-child{font-size:var(--ssm)}

.lds-facebook{display:inline-block;position:relative;width:80px;height:80px}.lds-facebook div{display:inline-block;position:absolute;left:8px;width:16px;background:var(--var-primary-color);-webkit-animation:lds-facebook 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;animation:lds-facebook 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite}.lds-facebook div:nth-child(1){left:8px;-webkit-animation-delay:-0.24s;animation-delay:-0.24s}.lds-facebook div:nth-child(2){left:32px;-webkit-animation-delay:-0.12s;animation-delay:-0.12s}.lds-facebook div:nth-child(3){left:56px;-webkit-animation-delay:0;animation-delay:0}@-webkit-keyframes lds-facebook{0%{top:8px;height:64px}50%,100%{top:24px;height:32px}}@keyframes lds-facebook{0%{top:8px;height:64px}50%,100%{top:24px;height:32px}}

.Form__input[type="number"]::-webkit-inner-spin-button,.Form__input[type="number"]::-webkit-outer-spin-button{-webkit-appearance:none;margin:0}.Form__input[type="number"]{-moz-appearance:textfield}.Form__input,select{width:100%;padding:12px 20px;margin:8px 0;display:inline-block;border:1px solid #ccc;border-radius:4px;box-sizing:border-box}.Form__btn[type="submit"]{width:100%;background-color:var(--var-primary-color);color:white;padding:14px 20px;margin:8px 0;border:none;border-radius:4px;cursor:pointer}.Form__btn--Confirm[type="submit"]{background-color:#00b418}.Form__btn[type="submit"]:hover{background-color:var(--var-secondary-color)}form{margin-top:30px;display:flex;justify-content:center}form .FormWrap{display:grid;grid-template-columns:repeat(3, 2fr) 1fr;grid-gap:2rem;gap:2rem}.infoErrorInput{color:var(--var-primary-color)}.ContactWrap{display:flex;flex-direction:column;align-items:center}.ContactWrap h1{margin-top:30px}.ContactWrap .ContactWrap__List{width:80%}.ContactWrap .ContactWrap__List img{width:25px;-webkit-filter:invert(48%) sepia(79%) saturate(5000%) hue-rotate(10deg) brightness(118%) contrast(119%);filter:invert(48%) sepia(79%) saturate(5000%) hue-rotate(10deg) brightness(118%) contrast(119%)}.ContactWrap .ContactWrap__List .Title__List{display:grid;grid-template-columns:0.5fr repeat(3, 2fr) 1fr;place-items:center;margin-top:30px;border-bottom:2px solid gray;padding-bottom:16px}.ContactWrap .ContactWrap__List .Title__List img:nth-child(1){width:15px}.ContactWrap .ContactWrap__List .CardContact{display:grid;grid-template-columns:0.5fr repeat(3, 2fr) 1fr;margin-top:8px;justify-items:center;align-items:center}.ContactWrap .ContactWrap__List .CardContact:hover{background-color:#e4e4e4}.ContactWrap .ContactWrap__List .CardContact:hover .btnEdit{display:block}.ContactWrap .ContactWrap__List .CardContact p{color:var(--var-secondary-color);font-size:var(--sm)}.ContactWrap .ContactWrap__List .CardContact p:nth-child(1){font-size:var(--ssm)}.ContactWrap .ContactWrap__List .CardContact .btnEdit{background:none;color:inherit;border:none;padding:0;font:inherit;cursor:pointer;outline:inherit;display:none}.ContactWrap .ContactWrap__List .CardContact .btnEdit:hover img{-webkit-filter:contrast(80%);filter:contrast(80%)}

.HomeContainer{display:grid;place-content:center;height:89.2vh;grid-template-columns:repeat(2, 300px);grid-gap:64px;gap:64px}.HomeContainer .HomeContainer__Cards{height:300px;background-color:var(--var-secondary-color);display:flex;align-items:center;justify-content:center;padding:20px;box-sizing:border-box;border-radius:16px;transition:0.3s;cursor:pointer;text-decoration:none;flex-direction:column}.HomeContainer .HomeContainer__Cards img{width:100%;height:100px}.HomeContainer .HomeContainer__Cards .HomeContainer__Cards--iconList{-webkit-filter:invert(100%);filter:invert(100%)}.HomeContainer .HomeContainer__Cards h1{color:var(--var-terceary-color);font-size:var(--md);letter-spacing:3px}.HomeContainer .HomeContainer__Cards:hover{box-shadow:0px 0px 7px #000;-webkit-transform:scale(1.05);transform:scale(1.05);background-color:black}@media (max-width: 900px){.HomeContainer{grid-template-columns:repeat(1, 250px);margin-top:50px;margin-bottom:50px}.HomeContainer .HomeContainer__Cards{height:250px}.HomeContainer .HomeContainer__Cards img{height:70px}}

.ImgError img{height:89.2vh}

